@font-face {
  font-family: "TTLakes";
  src: url(./fonts/TTLakes-Regular.woff) format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "TTLakes";
  src: url(./fonts/TTLakes-Medium.woff) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "TTLakes";
  src: url(./fonts/TTLakes-Bold.woff) format("truetype");
  font-weight: 700;
  font-style: normal; }

html,
body {
  height: 100%;
  min-width: 320px;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

ul {
  padding: 0;
  margin: 0; }

ul li {
  list-style: none;
  padding: 0; }

a {
  color: inherit; }

a:link,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

button {
  background-color: transparent;
  border: none;
  cursor: pointer; }

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-family: "TTLakes";
  font-size: 14px;
  position: relative; }
  .wrapper > main {
    flex: 1 1 auto; }
  .wrapper > * {
    min-width: 0; }

[class*="__container"] {
  max-width: 1392px;
  box-sizing: content-box;
  margin: 0 auto; }

body {
  background-color: #f8f8f8; }
  body.lock {
    overflow: hidden;
    touch-action: none; }

@media (min-width: 1365.98px) {
  .page {
    margin-left: 326px; } }

/*START======================Header======================================================================*/
.header {
  background-color: #ffffff;
  /* //fixed header right?
	@media (max-width: 1365.98px) {
		position: fixed;
		z-index: 20;
		width: 100%;
		top: 0;
		left: 0;
	}*/ }
  @media (min-width: 1365.98px) {
    .header {
      background-color: transparent; } }
