    z-index: 2;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0px;
    background-color: rgba(255, 255, 255, 0.9);
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease 0s; }
    @media (min-width: 359.98px) {
      .active .header__close-area {
        visibility: visible;
        opacity: 1; } }
    @media (min-width: 1365.98px) {
      .header__close-area {
        display: none; } }
  .header__container {
    padding: 0 15px;
    display: flex;
    align-items: center;
    min-height: 88px; }
    @media (min-width: 767.98px) {
      .header__container {
        padding: 0 24px; } }
    @media (min-width: 1365.98px) {
      .header__container {
        min-height: 0px; } }
  .header__open-burger {
    margin-right: 16px; }
    @media (min-width: 1365.98px) {
      .header__open-burger {
        display: none; } }
  .header__logo {
    padding-right: 16px;
    position: relative;
    flex: 0 0 96px; }
    .header__logo img {
      max-width: 100%;
      height: auto; }
    .header__logo::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 32px;
      top: 20%;
      right: 0;
      background-color: #eaeaea; }
    @media (min-width: 767.98px) {
      .header__logo {
        padding-left: 20px;
        padding-right: 0;
        margin-right: 16px; }
        .header__logo::after {
          left: 0; } }
    @media (min-width: 1365.98px) {
      .header__logo {
        display: none; } }
  .header__actions {
    display: flex;
    margin-left: auto; }
    @media (min-width: 1365.98px) {
      .header__actions {
        display: none; } }
  .header__action {
    display: none; }
    .header__action:nth-child(3) {
      margin-right: 20px; }
    .header__action:nth-child(4) {
      display: inline-block;
      margin-right: 16px;
      padding-left: 20px; }
    .header__action:nth-child(5) {
      display: inline-block; }
    @media (min-width: 767.98px) {
      .header__action:not(:last-child) {
        display: inline-block;
        margin-right: 16px; }
      .header__action:nth-child(4) {
        position: relative; }
        .header__action:nth-child(4)::after {
          content: "";
          position: absolute;
          width: 2px;
          height: 32px;
          top: 10%;
          left: 0;
          background-color: #eaeaea; } }
  .header__fixed {
    box-sizing: border-box;
    position: fixed;
    z-index: 3;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    padding: 24px 16px 32px;
    overflow-y: auto;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
    transition: left 0.5s ease 0s;
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .header__fixed::-webkit-scrollbar {
      width: 0;
      height: 0; }
    .active .header__fixed {
      left: 0;
      visibility: visible; }
    @media (min-width: 359.98px) {
      .header__fixed {
        width: 320px;
        padding: 24px 24px 32px; } }
    @media (min-width: 1365.98px) {
      .header__fixed {
        box-shadow: none;
        left: 0;
        position: absolute;
        visibility: visible; } }
  .header__top {
    display: flex;
    align-items: center;
    margin-bottom: 50px; }
  .header__close-burger {
    margin-right: 17px; }
    @media (min-width: 1365.98px) {
      .header__close-burger {
        display: none; } }
  .header__search {
    margin-left: auto; }
  .header__nav {
    flex: 1 1 auto; }
  .header__item:not(:last-child) {
    margin-bottom: 32px; }
  .header__link {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: -0.2px;
    position: relative;
    transition: color 0.3s ease 0s; }
    .header__link::before {
      content: "";
      display: none;
      position: absolute;
      top: -8px;
      left: -16px;
      width: 4px;
      height: 40px;
      background: url(./static/line.svg) 0 0 no-repeat; }
      @media (min-width: 359.98px) {
        .header__link::before {
          left: -24px; } }
    .header__link.active {
      color: #7e7e82; }
      .header__link.active::before {
        display: inline-block; }
  .header__bottom {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: -0.2px; }
  .header__langs {
    display: flex;
    align-items: center;
    flex-wrap: wrap; }
  .header__lang {
    margin-right: 8px;
    transition: color 0.3s ease 0s; }
    .header__lang.active {
      color: #7e7e82; }
  .header__mail {
    margin-bottom: 12px;
    display: block;
    transition: color 0.3s ease 0s; }
  .header__phone {
    display: block;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.6px;
    margin-bottom: 40px;
    transition: color 0.3s ease 0s; }
  .header__actions-fixed {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 16px;
    margin-top: 24px; }
  .header__action-fixed:not(:last-child) {
    margin-right: 16px; }

@media (any-hover: hover) {
  .header__link:hover,
  .header__phone:hover,
  .header__mail:hover,
  .header__lang:hover {
    color: #0da09b; } }

